

import { Component, OnInit } from "@angular/core";
import { OverdueHttpService } from "app/pages/tzb/custom/overdue/http/overdue.http.service";
import { CommonlyMessage } from "app/pages/tzb/custom/overdue/common/msg";
import { ObjectHelper, DateHelper } from "app/pages/tzb/custom/overdue/common/objecthelper";
import { Commfunc } from "app/@uisftech/common/service/commonFun";

@Component({
    selector: 'overdue-set-search',
    templateUrl: './overdue-set-search.component.html',
    styleUrls: ['./overdue-set-search.component.scss'],
    providers: [OverdueHttpService]
})
export class OverdueSetSearchComponent implements OnInit {

    commonlyMessage: CommonlyMessage = new CommonlyMessage()
    objectHelper: ObjectHelper = new ObjectHelper();
    dateHelper: DateHelper = new DateHelper();

    constructor(
        public httpService: OverdueHttpService,
        public commfunc: Commfunc,
    ) {
        
    }

    ngOnInit(): void {
        this.pageInit.init();
    }

    pageData = {
        parent: this,
        code: null,
        overdueRuleList: [],
        handlePsersonName: '',
        agentOrgName: '',
        createdStamp: '',
        fixCreatedStamp () {
            if (this.createdStamp) {
                this.createdStamp = this.parent.dateHelper.getDateString(this.createdStamp);
            }
        }
    }

    pageInit = {
        parent: this,
        init () {
            this.downLoadData();
            this.codeValues();
        },
        //码值
        codeValues() {
            this.parent.commfunc.codeValue();
            let ret = this.parent.commfunc.codeDatas;
            this.parent.pageData.code = ret
        },
        distribute (data) {
            let pageData = this.parent.pageData;
            for (let prop in data) {
                if (pageData.hasOwnProperty(prop)) {
                    pageData[prop] = data[prop]
                }
            }
        },
        downLoadData () {
            let params = {};
            this.parent.httpService.overduePushRuleQuery(params).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.distribute(data);
                    this.parent.pageData.fixCreatedStamp();
                } else {
                    this.parent.commonlyMessage.logicError(data.returnCode.message);
                }
            }, err => {
                this.parent.commonlyMessage.logicError('调用失败!');
            });
        }
    }

    dialog = {
        display: false,
        showDialog () {
            this.display = true;
        },
        hideDialog () {
            this.display = false;
        }
    }

    modify = {
        parent: this,
        currentIndex: 0,
        inputData: null,
        copyObj (source, target) {
            if (source && typeof source == 'object') {
                for (let prop in target) {
                    if (target.hasOwnProperty(prop)) {
                        target[prop] = source[prop];
                    }
                }
            }
        },
        setIndex (index) {
            this.currentIndex = index;
        },
        getCurrentItem () {
            return this.parent.pageData.overdueRuleList[this.currentIndex];
        },
        callDialog (params) {
            this.inputData = params;
            this.parent.dialog.showDialog();
        }
    }

    view = {
        parent: this,
        current: null,
        setCurrent () {
            let t = this.parent.modify.getCurrentItem();
            this.current = this.parent.objectHelper.objectDeepCopy(t);
        },
        viewClick (index) {
            this.parent.modify.setIndex(index);
            this.setCurrent();
            this.parent.modify.callDialog({
                data: this.current,
                mode: 'view'
            });
        }
    }

    backTo () {
        window.history.go(-1);
    }


}