import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'pipeWithMutiInput',
    pure: true
})

export class PipeWithMutiInput implements PipeTransform {
    name: string;

    getLabel (value, list) {
        for (let i in list) {
            if (value == list[i].value) {
                return list[i].label
            }
        }
    }

    transform (value: any, args: any[]): any {
        if (value && args) {
            let result = [];
            let arr = value.split(',');

            for (let i in arr) {
                let label = this.getLabel(arr[i], args);
                if (label) {
                    result.push(label);
                }
            }
            
            if (result && result.length) {
                return result.join(',');
            }
        }
    }
}