    download(item) {
        // if (item && item.caseDocument) {
        //     let source = item.caseDocument;
        //     let split = source.lastIndexOf('/') + 1;
        //     let url = source.substring(0, split);
        //     let filename = source.substring(split);
        //     window.open(this.DOWNLOAD + "?fileName=" + encodeURI(filename) + "&fileUrl=" + url, "_self");
        // }
        if (item) {
            let params = {
                overdueCaseAnalyzeId: item.inspectAnalyzeTaskId,
                overdueCaseName: '',
                custNo: item.custNo,
                custName: item.custName,
                caseDocument: item.caseDocument,
                contractNumber: item.contactId
            }
            this.httpService.overduePdf(params).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    let source = data.caseDocument;
                    let split = source.lastIndexOf('/') + 1;
                    let url = source.substring(0, split);
                    let filename = source.substring(split);
                    window.open(this.DOWNLOAD + "?fileName=" + encodeURI(filename) + "&fileUrl=" + url, "_self");
                } else {
                    this.commonlyMessage.logicError(data.returnCode.message);
                }
            }, err => {
                this.commonlyMessage.logicError('调用失败!');
            });
        }
    }

    export() {

        this.fixDate();

        let currentColumns = [];

        if (this.arrList.length) {
            this.arrList.forEach(element => {
                currentColumns.push({
                    ch: element.label,
                    en: element.value.replace('Desc', '')
                })
            })
        }


        let params = {
            overdueCaseList: currentColumns
        };

        Object.assign(params, this.overdueQueryBean);

        this.httpService.overdueCreditDownLoadInfo(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.commonlyMessage.logicSuccess(data.returnCode.message);
                this.router.navigate(['/pages/tzb/custom/downloadCenter/downCenter']);
            } else {
                this.commonlyMessage.logicError(data.returnCode.message);
            }
        }, err => {
            this.commonlyMessage.logicError('调用失败!');
        });
    }



    /**
     * 选择字段开始
     */

    //弹出选择字段模态框

    loadSelected() {
        this.arrList = [];
        this.allList.forEach(item => {
            if (item['isCheck']) {
                this.arrList.push(item);
            }
        });
    }

    //全选
    checkAll() {
        this.arrList = [];
        this.allList.forEach(item => {
            item['isCheck'] = this.isAll;
            if (this.isAll) {
                this.arrList.push(item);
            }
        });
    }

    //单选
    doSelect(data) {
        let check = true;
        if (data.isCheck) {
            this.arrList.push(data);
            this.allList.forEach(item => {
                check = item.isCheck && check;
            });
        } else {
            this.arrList.forEach((item, i) => {
                if (item.value == data.value) {
                    this.arrList.splice(i, 1);
                    return;
                }
            })
            check = false;
        }
        this.isAll = check;
        // console.log(this.arrList)
    }
    /**
    * 选择字段结束
    */


