import { OnInit, Component } from "@angular/core";
import { OverdueHttpService } from "app/pages/tzb/custom/overdue/http/overdue.http.service";
import { CommonlyMessage } from "app/pages/tzb/custom/overdue/common/msg";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { ConfirmationService } from "primeng/primeng";
import { LocalData } from "app/pages/tzb/custom/overdue/common/localData";
import { CustomCenterHttpService } from "app/pages/tzb/customCenter/http/custom-center.http.service";

@Component({
    selector: 'overdue-create',
    templateUrl: './overdue-create.component.html',
    styleUrls: ['./overdue-create.component.scss'],
    providers: [ OverdueHttpService, ConfirmationService, CustomCenterHttpService ]
})
export class OverdueCreateComponent implements OnInit {

    commonlyMessage: CommonlyMessage = new CommonlyMessage()
    localData: LocalData = new LocalData(this.commfunc);

    constructor (
        public confirmationService: ConfirmationService,
        public httpService: OverdueHttpService,
        public commfunc: Commfunc,
        public customCenterHttpService: CustomCenterHttpService,
    ) {

    }
    ngOnInit(): void {
        this.pageInit.init();
    }

    pageData = {
        code: null
    }


    pageInit = {
        parent: this,
        init () {
            this.codeValues();
            this.parent.query.query();
        },
        //码值
        codeValues() {
            this.parent.commfunc.codeValue();
            let ret = this.parent.commfunc.codeDatas;
            this.parent.pageData.code = ret
        }
    }

    query = {
        parent: this,
        businessType: '',
        overdueType: '',
        custId: '',
        custName: '',
        custNo:'',
        contractNumber: '',
        display: false,
        query () {
            this.parent.dataTable.pageNum = 1;
            this.parent.dataTable.overdueCreditListQuery();
        },
        customerPop () {
            this.showDialog();
        },
        customerCall (event) {
            this.custId = event['custNo'];
            this.custName = event['custName'];
            this.hideDialog();
        },
        showDialog () {
            this.display = true;
        },
        hideDialog () {
            this.display = false;
        },
        reset () {
            this.businessType = '';
            this.overdueType = '';
            this.custId = '';
            this.custName = '';

            this.query();
        }
    }

    dataTable = {
        parent: this,
        overdueCreditList: [],
        pageSize: 10,
        pageNum: 1,
        total: 1,
        first: 0,
        overdueCreditListQuery() {
            let query = this.parent.query;
            let params = {
                pageSize: this.pageSize,
                pageNum: this.pageNum,
                businessType: query.businessType,
                overdueType: query.overdueType,
                custId: query.custId,
                custName: query.custName,
                contractNumber: query.contractNumber
            };
            this.parent.httpService.overdueCreditListQuery(params).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.overdueCreditList = data.overdueCreditList;
                    this.total = data.total;
                } else {
                    this.parent.commonlyMessage.logicError(data.returnCode.message);
                }
            }, err => {
                this.parent.commonlyMessage.logicError('调用失败!');
            });
        },
        // 翻页
        paginate(event) {
            //每页显示的条数
            this.pageSize = event.rows * 1;
            //当前页
            this.pageNum = event.page + 1;
            this.first = event.page * this.pageSize;
            
            this.overdueCreditListQuery();
        },
        getNewSerialNumer (callback: Function) {
            let param = {}
            this.parent.customCenterHttpService.createGlobalSeqNo(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    let commonHeader = JSON.parse(this.parent.commfunc.getSessionData('commonHeader'));
                    commonHeader['globalSeqNo'] = data.globalSeqNo;
                    this.parent.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                    this.parent.commonlyMessage.logicSuccess(data.returnCode.message);
                } else {
                }
                callback();
            }, error => {
                callback();
            })
        },
        submit (col: any) {
            this.getNewSerialNumer(()=>{
                let params = {
                    overdueCaseAnalyzeId: col.inspectAnalyzeTaskId
                };
                this.parent.httpService.overdueCaseAdd(params).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.parent.commonlyMessage.logicSuccess(data.returnCode.message);
                    } else {
                        this.parent.commonlyMessage.logicError(data.returnCode.message);
                    }
                    this.parent.cover.hide();
                }, err => {
                    this.parent.cover.hide();
                    this.parent.commonlyMessage.logicError('调用失败!');
                });
            })
        },
        view (col: any) {
            this.callDialog({
                mode: 'view',
                data: col
            })
        },
        callDialog (params) {
            this.parent.dialog.setInput(params);
            this.parent.dialog.showDialog();
        },
        submitClick (col: any) {
            this.parent.confirmationService.confirm({
                message: '确定生成逾期吗？',
                accept: () => {
                  this.parent.cover.show();
                  this.submit(col);
                }
            });
        }
    }

    dialog = {
        display: false,
        showDialog () {
            this.display = true;
        },
        hideDialog () {
            this.display = false;
        },
        inputData: null,
        outputData: null,
        setInput (para: any) {
            this.inputData = para;
        },
        dialogCall (event) {
            this.hideDialog();
        }
    }

    cover = {
        maskLayer: false,
        show () {
            this.maskLayer = true;
        },
        hide () {
            this.maskLayer = false;
        }
    }


}