import { OnInit, Component, Input, Output, EventEmitter } from "@angular/core";
import { OverdueHttpService } from "app/pages/tzb/custom/overdue/http/overdue.http.service";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { DateHelper } from "app/pages/tzb/custom/overdue/common/objecthelper";


@Component({
    selector: 'overdue-detail',
    templateUrl: './overdue-detail.component.html',
    styleUrls: ['./overdue-detail.component.scss'],
    providers: [OverdueHttpService]
})
export class OverdueDetail implements OnInit {
    @Input() config: any;
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    dateHelper: DateHelper = new DateHelper();

    constructor (
        public httpService: OverdueHttpService,
        public commfunc: Commfunc,
    ) {

    }

    ngOnInit(): void {
        this.pageInit.init();
    }

    pageData = {
        parent: this,
        title: '',
        code: null,
        mode: '',
        data: {
            businessTypeDesc: '',
            overdueTypeDesc: '',
            custNo: '',
            custName: '',
            contactId: '',
            debtNo: '',
            loanAmount: '',
            loanBalance: '',
            overdueDays: '',
            productName: '',
            guarantyTypeDesc: '',
            parentBusinessTypeName: '',
            loanStartDateDesc: '',
            loanEndDateDesc: '',
            handlePsersonName: '',
            agentOrgName: '',
            advocateManagePersonName: '',
            advocateManageOrgName: '',
            overdueAmount: '',
            handlePsersonId: '',
            advocateManagePerson: ''
        },
        fixTitle (mode) {
            switch (mode) {
                case 'add':
                    this.title = '新增';
                break;
                case 'edit':
                    this.title = '修改';
                break;
                case 'view':
                    this.title = '详情';
                break;
            }
        },
        getLabel (val, list) {
            let ret = "";
            if (val && list && list.length) {
                let t = list.filter(item => item.value == val);
                if (t && t.length)  {
                    ret = t[0].label;
                }
            }
            return ret;
        },
        fixData () {
            this.data['businessTypeDesc'] = this.getLabel(this.data.businessType, this.code['overdueBusinessType']);
            this.data['overdueTypeDesc'] = this.getLabel(this.data.overdueType, this.code['overdueType']);
            this.data['guarantyTypeDesc'] = this.getLabel(this.data.guarantyType, this.code['GuaranteeMethod']);
            this.data['loanStartDateDesc'] = this.parent.dateHelper.getDate(this.data.loanStartDate);
            this.data['loanEndDateDesc'] = this.parent.dateHelper.getDate(this.data.loanEndDate);
        }
    }

    pageInit = {
        parent: this,
        init () {
            this.codeValues();
            this.grapInput();
        },
        //码值
        codeValues() {
            this.parent.commfunc.codeValue();
            let ret = this.parent.commfunc.codeDatas;
            this.parent.pageData.code = ret
        },
        grapInput () {
            let config = this.parent.config;
            if (config) {
                let pageData = this.parent.pageData;
                if (config.data) {
                    pageData.data = config.data;
                    pageData.fixData();
                }
                pageData.mode = config.mode;
                pageData.fixTitle(config.mode);
                
            }
        }
    }

    back () {
        this.outValue.emit(null);
    }

}