

import { Component, OnInit } from "@angular/core";
import { OverdueHttpService } from "app/pages/tzb/custom/overdue/http/overdue.http.service";
import { CommonlyMessage } from "app/pages/tzb/custom/overdue/common/msg";
import { ConfirmationService } from "primeng/primeng";
import { ObjectHelper } from "app/pages/tzb/custom/overdue/common/objecthelper";
import { Commfunc } from "app/@uisftech/common/service/commonFun";

@Component({
    selector: 'overdue-set',
    templateUrl: './overdue-set.component.html',
    styleUrls: ['./overdue-set.component.scss'],
    providers: [ OverdueHttpService, ConfirmationService ]
})
export class OverdueSetComponent implements OnInit {

    commonlyMessage: CommonlyMessage = new CommonlyMessage();
    objectHelper: ObjectHelper = new ObjectHelper();

    constructor(
        public confirmationService: ConfirmationService,
        public httpService: OverdueHttpService,
        public commfunc: Commfunc,
    ) {
        
    }

    ngOnInit(): void {
        this.pageInit.init();
    }

    pageData = {
        code: null,
        overdueRuleList: [],
        handlePsersonName: '',
        agentOrgName: '',
        createdStamp: ''
    }

    pageInit = {
        parent: this,
        init () {
            this.downLoadData();
            this.codeValues();
        },
        //码值
        codeValues() {
            this.parent.commfunc.codeValue();
            let ret = this.parent.commfunc.codeDatas;
            this.parent.pageData.code = ret
        },
        distribute (data) {
            let pageData = this.parent.pageData;
            for (let prop in data) {
                if (pageData.hasOwnProperty(prop)) {
                    pageData[prop] = data[prop]
                }
            }
        },
        toString (obj) {
            let spArr = [ 'totalBalanceLower', 'totalBalanceUpper', 'totalLimitLower', 'totalLimitUpper' ]
            for (let prop in obj) {
                if (obj.hasOwnProperty(prop)) {
                    if (spArr.indexOf(prop) > -1) {
                        obj[prop] = obj[prop].toString();
                    }
                }
            }
        },
        fixData (list) {
            if (list && list.length) {
                list.forEach(element => {
                    this.toString(element);
                });
            }
        },
        downLoadData () {
            let params = {};
            this.parent.httpService.overduePushRuleQuery(params).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.fixData(data.overdueRuleList);
                    this.distribute(data);
                } else {
                    this.parent.commonlyMessage.logicError(data.returnCode.message);
                }
            }, err => {
                this.parent.commonlyMessage.logicError('调用失败!');
            });
        }
    }


    dialog = {
        display: false,
        showDialog () {
            this.display = true;
        },
        hideDialog () {
            this.display = false;
        }
    }

    modify = {
        parent: this,
        currentIndex: 0,
        inputData: null,
        copyObj (source, target) {
            if (source && typeof source == 'object') {
                for (let prop in target) {
                    if (target.hasOwnProperty(prop)) {
                        target[prop] = source[prop];
                    }
                }
            }
        },
        setIndex (index) {
            this.currentIndex = index;
        },
        getCurrentItem () {
            return this.parent.pageData.overdueRuleList[this.currentIndex];
        },
        callDialog (params) {
            this.inputData = params;
            this.parent.dialog.showDialog();
        },
        modifyData (event) {
            debugger;
            this.parent.dialog.hideDialog();
            let mode = event.mode;
            let data = event.data;
            if (mode == 'add') {
                this.parent.add.addSet(data);
            } else if (mode == 'edit') {
                this.parent.edit.editSet(data);
            }
        }
    }

    add = {
        parent: this,
        addClick () {
            this.parent.modify.callDialog({
                data: null,
                mode: 'add'
            });
        },
        addSet (event) {
            let pageData = this.parent.pageData;
            let overdueRuleList = pageData.overdueRuleList;
            let obj = {
                businessType: '',
                overdueType: '',
                totalLimitLower: '',
                totalLimitUpper: '',
                totalBalanceLower: '',
                totalBalanceUpper: '',
                overdueDays: '',
                tenLevelClass: '',
                priorityOrder: '',
                innerPriorityOrder: '',
                pushDeadlineCtrl: '',
                pushDeadline: '',
                contactPushCtrl: '',
                taskOverdueDays: ''
            };
            this.parent.modify.copyObj(event, obj);
            this.parent.pageData.overdueRuleList = overdueRuleList.concat([obj]);
        }
    }

    delete = {
        parent: this,
        deleteClick (index) {
            this.parent.confirmationService.confirm({
                message: '确定删除吗该配置吗？',
                accept: () => {
                  this.deleteSet(index);
                }
            });
        },
        deleteSet (index) {
            debugger;
            let pageData = this.parent.pageData;
            let overdueRuleList = pageData.overdueRuleList;
            overdueRuleList.splice(index, 1);
            this.parent.pageData.overdueRuleList = overdueRuleList.concat([]);
        }
    }

    view = {
        parent: this,
        current: null,
        setCurrent () {
            let t = this.parent.modify.getCurrentItem();
            this.current = this.parent.objectHelper.objectDeepCopy(t);
        },
        viewClick (index) {
            this.parent.modify.setIndex(index);
            this.setCurrent();
            this.parent.modify.callDialog({
                data: this.current,
                mode: 'view'
            });
        }
    }

    edit = {
        parent: this,
        current: null,
        setCurrent () {
            let t = this.parent.modify.getCurrentItem();
            this.current = this.parent.objectHelper.objectDeepCopy(t);
        },
        editClick (index) {
            this.parent.modify.setIndex(index);
            this.setCurrent();
            this.parent.modify.callDialog({
                data: this.current,
                mode: 'edit'
            });
        },
        editSet (event) {
            debugger;
            let index = this.parent.modify.currentIndex;
            let pageData = this.parent.pageData;
            let overdueRuleList = pageData.overdueRuleList;
            overdueRuleList[index] = event;
            this.parent.pageData.overdueRuleList = overdueRuleList.concat([]);
        }
    }


    submit () {
        let params = {
            overdueRuleList: this.pageData.overdueRuleList
        }
        this.httpService.overduePushRuleSet(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.commonlyMessage.logicSuccess(data.returnCode.message);
            } else {
                this.commonlyMessage.logicError(data.returnCode.message);
            }
        }, err => {
            this.commonlyMessage.logicError('调用失败!');
        });
    }


}