import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { OverdueComponent } from './overdue.component';//逾期首页
import { OverdueQuery } from './overdue-query/overdue-query.component';//逾期案例分析查询
import { OverdueList } from './overdue-list/overdue-list.component';//逾期案例列表查询
import { OverdueCase } from './overdue-case/overdue-case.component';//六个tabs切换列表
import { OverdueSetComponent } from "app/pages/tzb/custom/overdue/overdue-set/overdue-set.component";
import { OverdueSetSearchComponent } from "app/pages/tzb/custom/overdue/overdue-set-search/overdue-set-search.component";
import { OverdueCreateComponent } from "app/pages/tzb/custom/overdue/overdue-create/overdue-create.component";


export const routes: Routes = [
    {
        path: '',
        component: OverdueComponent,
        children: [
            { path: '', redirectTo: 'overdue-case', pathMatch: 'full' },
            { path: 'overdue-case', loadChildren: './overdue-case/overdue-case.module#OverdueCaseModule' },//逾期
        ]
    },
    {
        path:'overdue-query',
        component:OverdueQuery
    },
    {
        path:'overdue-list',
        component:OverdueList
    },
    {
        path: 'overdue-set',
        component: OverdueSetComponent
    },
    {
        path: 'overdue-set-search',
        component: OverdueSetSearchComponent
    },
    {
        path: 'overdue-create',
        component: OverdueCreateComponent
    }
]
export const OverdueRouting: ModuleWithProviders = RouterModule.forChild(routes);