
import { OnInit, Component, Input, Output, EventEmitter } from "@angular/core";
import { OverdueHttpService } from "app/pages/tzb/custom/overdue/http/overdue.http.service";
import { CommonlyMessage } from "app/pages/tzb/custom/overdue/common/msg";
import { Commfunc } from "app/@uisftech/common/service/commonFun";

@Component({
    selector: 'overdue-set-add',
    templateUrl: './overdue-set-add.component.html',
    styleUrls: ['./overdue-set-add.component.scss'],
    providers: [OverdueHttpService]
})
export class OverdueSetAdd implements OnInit {
    @Input() config: any;
    @Output() outValue: EventEmitter<any> = new EventEmitter();

    commonlyMessage: CommonlyMessage = new CommonlyMessage()

    constructor (
        public httpService: OverdueHttpService,
        public commfunc: Commfunc,
    ) {

    }

    ngOnInit(): void {
        this.pageInit.init();
    }

    pageData = {
        parent: this,
        title: '',
        code: null,
        mode: '',
        data: {
            businessType: '',
            overdueType: '',
            totalLimitLower: '0.00',
            totalLimitUpper: '9999999999999.99',
            totalBalanceLower: '0.00',
            totalBalanceUpper: '9999999999999.99',
            overdueDays: '',
            tenLevelClass: '',
            priorityOrder: '',
            innerPriorityOrder: '',
            pushDeadlineCtrl: '',
            pushDeadline: '',
            contactPushCtrl: '',
            taskOverdueDays: ''
        },
        tenLevelClassList: [],
        setTenLevelClassList (source) {
            if (source) {
                this.tenLevelClassList = source.split(',');
            }
        },
        overdueDay: '',
        overdueTerm: '',
        fixData () {
            // let spArr = [ 'priorityOrder', 'overdueDays', 'taskOverdueDays' ]
            if (!isNaN(this.data.priorityOrder)) {
                this.data.priorityOrder = parseInt(this.data.priorityOrder);
            }
            if (this.tenLevelClassList && this.tenLevelClassList.length) {
                this.data.tenLevelClass = this.tenLevelClassList.join(',');
            }
            if (this.overdueDay) {
                this.data.overdueDays = this.overdueDay + ',' + this.overdueTerm;
            }
        },
        fixOverdueDays () {
            if (this.data.overdueDays) {
                let t = this.data.overdueDays.split(',');
                this.overdueDay = t[0];
                this.overdueTerm = t[1];
            }
        },
        fixDecimals () {
            this.parent.domEvent.totalLimitLowerBlur(null);
            this.parent.domEvent.totalLimitUpperBlur(null);
            this.parent.domEvent.totalBalanceLowerBlur(null);
            this.parent.domEvent.totalBalanceUpperBlur(null);
        },
        fixNumber (num) {
            if (isNaN(num)) {
                return '0.00';
            }
            return Number.parseFloat(num).toFixed(2);
        },
        fixTitle (mode) {
            switch (mode) {
                case 'add':
                    this.title = '新增';
                break;
                case 'edit':
                    this.title = '修改';
                break;
                case 'view':
                    this.title = '详情';
                break;
            }
        }
    }

    pageInit = {
        parent: this,
        init () {
            this.codeValues();
            this.grapInput();
        },
        //码值
        codeValues() {
            this.parent.commfunc.codeValue();
            let ret = this.parent.commfunc.codeDatas;
            this.parent.pageData.code = ret
        },
        grapInput () {
            let config = this.parent.config;
            if (config) {
                let pageData = this.parent.pageData;
                if (config.data) {
                    pageData.data = config.data;
                    this.parent.pageData.setTenLevelClassList(config.data.tenLevelClass);
                }
                pageData.mode = config.mode;
                this.parent.pageData.fixOverdueDays();
                this.parent.pageData.fixTitle(config.mode);
                this.parent.pageData.fixDecimals();
            }
        }
    }



    cofirm () {
        let ret = this.validate.handle();
        if (ret) {
            this.commonlyMessage.logicError(ret);
            return;
        }
        this.pageData.fixData()
        this.outValue.emit(this.pageData);
    }


    domEvent = {
        parent: this,
        pushDeadlineChange (event) {
            if (this.parent.pageData.data.pushDeadlineCtrl == '0') {
                this.parent.pageData.data.pushDeadline = '';
            }
        },
        pushDeadlineTxtBlur (event) {
            // let ret = true;
            // let t = this.parent.pageData.data.pushDeadline;
            // if (!(/^[\d]+[D|M|Y]$/.test(t))) {
            //     this.parent.commonlyMessage.logicError('推送控制期限格式有误！');
            //     ret = false;
            // }
            // return ret;
            let t = this.parent.validate.pushDeadline();
            if (t) {
                this.parent.commonlyMessage.logicError(t);
            }
        },
        totalLimitLowerBlur (event) {
            this.parent.pageData.data.totalLimitLower = this.parent.pageData.fixNumber(this.parent.pageData.data.totalLimitLower);
        },
        totalLimitUpperBlur (event) {
            this.parent.pageData.data.totalLimitUpper = this.parent.pageData.fixNumber(this.parent.pageData.data.totalLimitUpper);
        },
        totalBalanceLowerBlur (event) {
            this.parent.pageData.data.totalBalanceLower = this.parent.pageData.fixNumber(this.parent.pageData.data.totalBalanceLower);
        },
        totalBalanceUpperBlur (event) {
            this.parent.pageData.data.totalBalanceUpper = this.parent.pageData.fixNumber(this.parent.pageData.data.totalBalanceUpper);
        },
    }

    validate = {
        parent: this,
        notNull (value, msg) {
            return /^\s*$/.test(value) ? msg : '';
        },
        businessType () {
            let value = this.parent.pageData.data.businessType;
            return this.notNull(value, '业务类型不能为空！');
        },
        overdueType () {
            let value = this.parent.pageData.data.overdueType;
            return this.notNull(value, '逾期类型不能为空！');
        },
        totalLimitLower () {
            let value = this.parent.pageData.data.totalLimitLower;
            return this.notNull(value, '授信累计额度下限不能为空！');
        },
        totalLimitUpper () {
            let value = this.parent.pageData.data.totalLimitUpper;
            return this.notNull(value, '授信累计额度上限不能为空！');
        },
        totalBalanceLower () {
            let value = this.parent.pageData.data.totalBalanceLower;
            return this.notNull(value, '授信累计余额下限不能为空！');
        },
        totalBalanceUpper () {
            let value = this.parent.pageData.data.totalBalanceUpper;
            return this.notNull(value, '授信累计余额上限不能为空！');
        },
        overdueDay () {
            let value = this.parent.pageData.overdueDay;
            return this.notNull(value, '逾期天数/期数第一项不能为空！');
        },
        priorityOrder () {
            let value = this.parent.pageData.data.priorityOrder;
            return this.notNull(value, '优先顺序不能为空！');
        },
        innerPriorityOrder () {
            let value = this.parent.pageData.data.innerPriorityOrder;
            return this.notNull(value, '同一优先级内顺序不能为空！');
        },
        pushDeadlineCtrl () {
            let value = this.parent.pageData.data.pushDeadlineCtrl;
            return this.notNull(value, '推送期限控制不能为空！');
        },
        pushDeadline () {
            debugger;
            let ret = '';
            if (this.parent.pageData.data.pushDeadlineCtrl && this.parent.pageData.data.pushDeadlineCtrl != '0') {
                let value = this.parent.pageData.data.pushDeadline;
                ret = this.notNull(value, '推送控制期限不能为空！');
                if (!ret) {
                    if (!(/^[\d]+[D|M|Y]$/.test(value))) {
                        ret = '推送控制期限格式有误！';
                    }
                }
            }
            return ret;
        },
        contactPushCtrl () {
            let value = this.parent.pageData.data.contactPushCtrl;
            return this.notNull(value, '合同推送控制不能为空！');
        },
        handle () {
            let ret = '';
            let exception = [ 'handle', 'parent', 'notNull' ];
            
            for (let item in this) {
                if (this.hasOwnProperty(item) && exception.indexOf(item) == -1) {
                    ret = this[item]();
                    if (ret) {
                        break;
                    }
                }
            }

            return ret;
        }
    }
}